/* eslint-disable no-new */
/* eslint-disable no-undef */
import Text from './Text';

new Vue({
  el: '#amp-ca-text',
  render(h) {
    return h(Text, {
      props: {
        content: (this.$el.attributes['data-content'] && this.$el.attributes['data-content'].value.length > 0 ? this.$el.attributes['data-content'].value : {})
      }
    });
  }
});



