import ContentService from '../../assets/js/amplience-content-service';

export const makeText = ({ text, fontSize, colour, font, uppercase }) => ({
  text,
  fontSize,
  colour,
  font,
  isUppercase: uppercase
});

export default class TextContentService extends ContentService {
  formatTextContent() {
    return this.getContent().then((formatted) => {
      this.content = makeText(formatted);
    });
  }
}



