<template>
  <p :style="style">{{ textContent.text }}</p>  
</template>

<script>
  import Content from "./text-content";
  import { Props, Computed } from '../../assets/js/mixins';
  
  let textContent;

  export default {
    mixins: [Props, Computed],
    data() {
      return {
        textContent: {}
      }
    },
    computed: {
      style() {
        return {
          color: this.textContent.colour,
          fontSize: this.textContent.fontSize + 'px',
          textTransform: this.textContent.isUppercase ? 'uppercase' : 'none',
          fontFamily: this.textContent.font
        }
      }
    },
    created() {
      if (this.hasContentUrl) {
        textContent = new Content(this.content);
      }
    },
    beforeMount() {
      if (this.hasContentUrl) {
        let vm = this;
        textContent.formatTextContent().then(() => {
          vm.textContent = textContent.content
        }, () => {
          vm.hasError = true;
        });
      } else {
        this.textContent = this.content
      }
    }
  }
</script>



